.search-input-modal {
    position: relative !important;
    margin-top: 5px;
    min-height: 38px;
    max-height: 38px;
    width: 100%;
    display: flex;
    align-items: center;
    top: 0px;
    left: 0;
    background: #F3F6F8;
    border-radius: 24px;
}


.search-icon-modal {
    position: absolute;
    left: 14px;
    width: 12.5px;
    height: 12.5px;
    top: 8px;
}

.search-input-field-modal {
    /* position: relative; */
    position: absolute;
    padding-left: 40px;
    padding-right: 108px;
    left: 0;
    top:0;
    width: 100%;
    height: 40px;
    /* min-width: 245px; */
    /* min-height: 30px; */
    outline: none;
    border: none;
    background-color: transparent;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    /* identical to box height, or 171% */


    /* Gray/G100 */

    color: #8F92A1;
    border: 0;
}